import http from "../utils/request"
export default {
	data() {
		return {
			userInfo: {},
			code: "",
			show:true,
		};
	},
	//先是this.getUserInfo(),之后是this.login(),之后this.getToken()
	methods: {
		getUserInfo() {
			//uniapp最新的获取用户信息的方式，旧版已获取不到
			uni.getUserProfile({
				desc: 'Wexin', // 这个参数是必须的
				success: (res) => {
					//console.log("用户信息",res.userInfo)
					//获得openID需要先用wx.login()获取临时登录凭证code
					this.userInfo = res.userInfo;
					this.login();
				},
				fail: (res) => {
					if (res.errMsg && (res.errMsg.indexOf("ok") !== -1)) {
						this.userInfo = res.userInfo
						this.login();
					}
				}
			})
		},
		login() {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					this.code = res.code;
					uni.showToast({
					    title: '登录中',
					    duration: 600,
						icon:'loading',
						position:'center',
					});
					this.getToken()
				},
				fail: (res) => {
					if (res.errMsg && res.errMsg.indexOf("ok") !== -1) {
						this.code = res.code
						this.getToken()
					}
				}
			});
		},
		getToken() {
			let reqData = {
				username: this.userInfo.nickName,
				avatar: this.userInfo.avatarUrl
			};
			//reqData = JSON.stringify(reqData) 
			uni.request({
				url: `https://story.genielink.cn/api/v1/getToken?code=${this.code}`,
				method: 'POST',
				data: reqData,
				success: res => {
					if (!res) {
						return uni.showToast({
							icon: 'loading',
							title: '获取数据失败'
						})
					}
					if (res.statusCode == 500) {
						return uni.showToast({
							icon: 'loading',
							title: '获取数据失败'
						})
					}
					if (res.data) {
						const data = {
							userName: this.userInfo.nickName,
							avatar: this.userInfo.avatarUrl,
							desc: res.data.desc,
							token: res.data.token
						}
						console.log('token',res.data.token)
						this.$store.commit('login', data)
					}
					this.cancel()
				},
				fail: err => {
					return uni.showToast({
						icon: 'loading',
						title: '请求失败'
					})
				}
			})

		},
		

		cancel() {
			this.$emit('cancelChild', false)
		},
	}
}
